var menu = document.getElementById('menu');
var opened = document.getElementById('menu--open')
var header = document.getElementById('header');
var form = document.getElementById('form');
var memo = 0;
menu.addEventListener('click', function() {
  opened.classList.toggle('is-open');
  memo = 1;
});

//closes the burger menu when a list item is clicked
header.addEventListener('click', function(){
if (memo == 1) {
    opened.classList.remove('is-open');
  }
})

form.addEventListener('click', function(){
  if (memo == 1) {
      opened.classList.remove('is-open');
  }
})

//to avoid resizing and having the menu open
function myFunction(x) {
	if (x.matches || y.matches) { // If media query matches
		opened.classList.remove('is-open');
	}
}

var x = window.matchMedia("(min-width: 860px)")
myFunction(x) // Call listener function at run time
x.addListener(myFunction) // Attach listener function on state changes 

var y = window.matchMedia("(max-width: 860px)")
myFunction(x) // Call listener function at run time
x.addListener(myFunction) // Attach listener function on state changes 